@use 'globals' as g;

#news{
  background-color: g.$light;
  padding-top: 4rem;
  padding-bottom: 4rem;
}


.card{
  border-radius: 0;
  background-color: white;
  border: g.$light 1rem solid;
  box-shadow: 5px 10px 18px #888888;
  
}

.card-top{
    height: 15rem; 
    width: 100%;
    
}



.card-img-top{
  object-fit: cover;
    object-position: center;
    border-radius: 0;
    min-height: 40%;
}

.news-row{
  height: fit-content;
}

.card-body{
  min-height: 11rem;
}

/*.card-body{
  pointer-events: none;
}*/

#news-card-container{
  position: relative;
}

.news-date{
  margin-top: 1rem;
  margin-bottom: 0;
}